___CSS_LOADER_EXPORT___.push([module.id, `.m-pg-board-container {
    position: relative;
    background-color: #fff;
    border-radius :8px;
    user-select: none; /* Prevent text selection */
    -webkit-user-select: none; /* For Safari */
    -ms-user-select: none; /* For older versions of IE */
    display: flex;
  }
  .m-pg-board {
    position: relative;
    background-color: #D3D3D3;
    border-radius :8px;
    display: flex;
    flex-direction: column;
    transition: ease-in-out 0.2s;
  }
  
  .m-pg-board > .m-pg-card {
    border-radius: 4px;
    padding: 8px;
    background-color: #fff;
    width: calc(100% - 64px);
    height: 90px;
    cursor: pointer;
    position: absolute;
    transition: all 0.1s linear;
    z-index: 9;
  }
  .m-pg-board > .m-pg-card.m-pg-card-active {
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
    z-index: 99;
    transform: scale(1.05);
    cursor: grabbing; 
  }
  .m-pg-card-title {
    font-size: 1.125rem;
    font-weight: 600;
    margin: 0;
    padding: 0;
  }
  body {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  .scaleX {
    transform: scaleX(1.2);
  }`, "",{"version":3,"sources":["webpack://./src/multiple-board/styles.css"],"names":[],"mappings":"AAAA;IACI,kBAAkB;IAClB,sBAAsB;IACtB,kBAAkB;IAClB,iBAAiB,EAAE,2BAA2B;IAC9C,yBAAyB,EAAE,eAAe;IAC1C,qBAAqB,EAAE,6BAA6B;IACpD,aAAa;EACf;EACA;IACE,kBAAkB;IAClB,yBAAyB;IACzB,kBAAkB;IAClB,aAAa;IACb,sBAAsB;IACtB,4BAA4B;EAC9B;;EAEA;IACE,kBAAkB;IAClB,YAAY;IACZ,sBAAsB;IACtB,wBAAwB;IACxB,YAAY;IACZ,eAAe;IACf,kBAAkB;IAClB,2BAA2B;IAC3B,UAAU;EACZ;EACA;IACE,4EAA4E;IAC5E,WAAW;IACX,sBAAsB;IACtB,gBAAgB;EAClB;EACA;IACE,mBAAmB;IACnB,gBAAgB;IAChB,SAAS;IACT,UAAU;EACZ;EACA;IACE,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,aAAa;EACf;EACA;IACE,sBAAsB;EACxB","sourcesContent":[".m-pg-board-container {\n    position: relative;\n    background-color: #fff;\n    border-radius :8px;\n    user-select: none; /* Prevent text selection */\n    -webkit-user-select: none; /* For Safari */\n    -ms-user-select: none; /* For older versions of IE */\n    display: flex;\n  }\n  .m-pg-board {\n    position: relative;\n    background-color: #D3D3D3;\n    border-radius :8px;\n    display: flex;\n    flex-direction: column;\n    transition: ease-in-out 0.2s;\n  }\n  \n  .m-pg-board > .m-pg-card {\n    border-radius: 4px;\n    padding: 8px;\n    background-color: #fff;\n    width: calc(100% - 64px);\n    height: 90px;\n    cursor: pointer;\n    position: absolute;\n    transition: all 0.1s linear;\n    z-index: 9;\n  }\n  .m-pg-board > .m-pg-card.m-pg-card-active {\n    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;\n    z-index: 99;\n    transform: scale(1.05);\n    cursor: grabbing; \n  }\n  .m-pg-card-title {\n    font-size: 1.125rem;\n    font-weight: 600;\n    margin: 0;\n    padding: 0;\n  }\n  body {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    height: 100vh;\n  }\n  .scaleX {\n    transform: scaleX(1.2);\n  }"],"sourceRoot":""}]);
